export default {
	props: ['user', 'pages'],
	data() {
		return {
			style1: 'border-bottom: 3px solid #EE9900;',
			style2: '',
			style3: '',
			style4: 'background: red;',
			changeText: '关注',
			imgSrc: ''
		};
	},
	methods:{
		click(res) {
			switch (res){
				case '作品':
					this.style1 = 'border-bottom: 3px solid #EE9900;';
					this.style2 = '';
					this.style3 = '';
					this.$emit('change', res);
					break;
				case '动态':
					this.style1 = '';
					this.style2 = 'border-bottom: 3px solid #EE9900;';
					this.style3 = '';
					this.$emit('change', res);
					break;
				case '喜欢':
					this.style1 = '';
					this.style2 = '';
					this.style3 = 'border-bottom: 3px solid #EE9900;';
					this.$emit('change', res);
					break;
				default:
					break;
			}
		},
		change() {
			this.style4 = this.style4 === 'background: red;'? '' : 'background: red;';
			this.changeText = this.changeText === '关注' ? '取消关注' : '关注';
		}
	}
}
